import * as THREE from 'three'
import vertexShader from './Shaders/Particles/vertex.glsl'
import fragmentShader from './Shaders/Particles/fragment.glsl'
import Experience from './Experience.js'
import { BufferGeometry } from 'three'

export default class Particles {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.resources = this.experience.resources
        this.time = this.experience.time

        this.count = 100000

        this.setGeometry()
        this.setMaterial()
        this.setPoints()
    }

    setGeometry() {
        this.geometry = new BufferGeometry()
        const positionArray = new Float32Array(this.count * 3)
        const scales = new Float32Array(this.count * 1)

        for (let i = 0; i < this.count; i++) {
            positionArray[i * 3 + 0] = (Math.random() - 0.5) * 120
            positionArray[i * 3 + 1] = (Math.random() - 0.5) * 120
            positionArray[i * 3 + 2] = (Math.random() - 0.5) * 120

            scales[i] = Math.random()
        }

        this.geometry.setAttribute('position', new THREE.Float32BufferAttribute(positionArray, 3))
        this.geometry.setAttribute('aScale', new THREE.BufferAttribute(scales, 1))
    }

    setMaterial() {
        this.material = new THREE.ShaderMaterial({
            transparent: true,
            blending: THREE.AdditiveBlending,
            uniforms: {
                uSize: { value: 80.0 * Math.min(window.devicePixelRatio, 2)},
                uTexture: {value: this.resources.items.dropTexture}
            },
            vertexShader: vertexShader,
            fragmentShader: fragmentShader,
        })  
    }

    setPoints() {
        this.points = new THREE.Points(this.geometry, this.material)
        this.scene.add(this.points)
    }

    update() { }
}