import * as THREE from 'three'
import vertexShader from './Shaders/Water/vertex.glsl'
import fragmentShader from './Shaders/Water/fragment.glsl'
import Experience from './Experience.js'

export default class Tube {
    constructor() {
        this.experience = new Experience()
        this.scene = this.experience.scene
        this.camera = this.experience.camera
        this.time = this.experience.time

        this.setGeometry()
    }
    setGeometry() {
        this.tunnel = {}

        this.tunnel.percentage = 0

        this.tunnel.points = [
            [10.5, 2, 14.5],
            [19.5, 0, 5.5],
            [38, 0, 10],
            [52, 3, 27],
            [43, -1, 50],
            [15.5, 1, 59.5],
            [1.5, 0, 43],
            [10.5, 2, 14.5],]

        for (let i = 0; i < this.tunnel.points.length; i++) {
            let x = this.tunnel.points[i][0]
            let y = this.tunnel.points[i][1]
            let z = this.tunnel.points[i][2]
            this.tunnel.points[i] = new THREE.Vector3(x, y, z);
        }

        this.tunnel.path = new THREE.CatmullRomCurve3(this.tunnel.points)

        this.tunnel.geometry = new THREE.TubeGeometry(this.tunnel.path, 128, 4, 32, true)

        this.tunnel.material = new THREE.ShaderMaterial({
            side: THREE.BackSide,
            uniforms: {
                uTime: {value: 0},

                uWavesElevation: { value: 0.001},
                uWavesFrequency: {value: new THREE.Vector2(2.0, 1.0)},
                uWavesSpeed: {value: 0.2},

                uDepthColor: {value: new THREE.Color(0xFFA000)},
                uSurfaceColor: {value: new THREE.Color(0xFFA000)},
            },
            vertexShader: vertexShader,
            fragmentShader: fragmentShader,
        })
        this.tunnel.tube = new THREE.Mesh(this.tunnel.geometry, this.tunnel.material)
        this.scene.add(this.tunnel.tube)

    }

    update() {
        this.tunnel.material.uniforms.uTime.value

        this.tunnel.percentage += 0.001
        this.tunnel.p1 = this.tunnel.path.getPointAt(this.tunnel.percentage % 1)
        this.tunnel.p2 = this.tunnel.path.getPointAt((this.tunnel.percentage + 0.1) % 1)
        this.camera.instance.position.set(this.tunnel.p1.x, this.tunnel.p1.y, this.tunnel.p1.z)
        this.camera.instance.lookAt(this.tunnel.p2)
    }
}